.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html,
body {
  height: 100%;
}

ul.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  /* background-color: #000000cc; */
}

li.menu {
  float: left;
  border-right: 1px solid #bbb;
}

li.menu:first-child {
  padding-left: 25%;
}

li.menu:last-child {
  /* border-right: none; */
}

li.menu a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  margin-bottom: 0.2rem;
}

li.menu a:hover:not(.active) {
  background-color: white;
  color: #333;
}

li.menu .active {
  background-color: #ff0000;
}

.center {
  margin: 5rem auto;
  width: 70%;
  border: 1px solid #9c9c9c;
  padding: 1rem;
}

.btn-padding {
  padding: 1rem;
  margin: 1rem;
}

.btn-margin-top {
  margin-top: 1rem;
}

.topnav-right {
  float: right !important;
}

.red {
  color: #ff0000;
}

.black {
  color: #000000;
}

.hide {
  visibility: hidden;
}

.show {
  visibility: visible;
}

.spinCenter {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.columnColor {
  background-color: #ff0000;
  margin: 5px 5px;
  padding: 5px;
  color: white;
  border-radius: 0.3rem;
}

